// 库文件
import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

// 随同首页一起加载的
import Home from '@/components/home'


// 按需加载类型，即网页打开后需要这个模块时才发送请求，不是一开始就加载进网页
// const Foo = resolve => require(['./Foo.vue'], resolve)
const Hello = resolve => require(['@/components/hello'], resolve);
const NotFind = (resolve) => {
  import('@/components/not-find').then((module) => {
    resolve(module)
  })
}
// 以上两种类型都是懒加载，统一使用第一种
 
export default new Router({
  routes: [
    {path:'/', name:'home', component:Home},
    {
      path: '/hello',
      name: 'hello',
      component: Hello,
      children:[
      ]
    },
    

    // 未找到路由页面
    { path: '/404', name:'not-find', component: NotFind }
  ]
})



