<template>
  <div id="app" class="container row col-md-offset-1 col-md-10">
    <nav-component></nav-component>
    <div class="row center">
        <router-view></router-view>
        <p @click="getmethod">click this to get</p>
    </div>
    <footer-component></footer-component>
  </div>
</template>

<script>
import FooterComponent from '@/components/comment/footer'
import NavComponent from '@/components/comment/header'

export default {
  name: 'app',
  data: function () {
    return {
      mdData: 'please wirt a moment'
    }
  },
  methods: {
    getmethod(){
      this.$http.get("/api/getAllCity")
        .then(res => {
          console.log(res.data)
        });
    }
  },
  components: { FooterComponent, NavComponent }
}
</script>

<style>
body {
  background-color: #EAE7E7;
}
.center {
    margin-top: 9px;
    margin-bottom: 63px;
    min-height: 400px;

}
</style>



