<template>
		<div class="col-md-12 course">
			<div class="carousel slide" id="carousel-331886">
				<ol class="carousel-indicators">
					<li v-for="index in indexs" v-bind:class="{ active: cur == index }" @click="change(index)">
					</li>
				</ol>
				<div class="carousel-inner">
					
					<div v-for="(item, index, key) in items" :class="['item', (index == cur) ? 'active' : '']">
						<img alt="" src="static/images/logo.png" :style="{height: courseHeight}"/>
						<div class="carousel-caption">
							<h4>
								Thumbnail label is {{ index + 1 }}
							</h4>
							<p>
								Cras justo odio, dapibus ac facilisis in, egestas eget quam. Donec id elit non mi porta gravida at eget metus. Nullam id dolor id nibh ultricies vehicula ut id elit.
							</p>
						</div>
					</div>

				</div> <a class="left carousel-control"  v-on:click="change(--cur)"><span class="glyphicon glyphicon-chevron-left"></span></a> <a class="right carousel-control" v-on:click="change(++cur)"><span class="glyphicon glyphicon-chevron-right"></span></a>
			</div>
		</div>


</template>

<script>
/**
 * TODO next and prv event
 */
export default {
    data() {
        return {
            cur: 1,
            indexs: [0, 1, 2],
            items: [
                {img: '../assets/bar.png', content: "hello"},
                {img: '../assets/bar.png', content: "hello"},
                {img: '../assets/bar.png', content: "hello"}
            ]
        };
    },
	props: ['courseHeight'],
	methods: {
		change: function (newcur) {
			this.cur = (newcur < 0) ? this.indexs.length - 1 : newcur;
			this.cur = newcur === this.indexs.length ? 0 : this.cur;
			console.log("new cur:", newcur, "  cur:" , this.cur);
		}
	}
};
</script>



