<template>
    <div id="header" class="row">
       <div class="col-md-5">
            <img v-bind:src="logoSrc" v-bind:style="{width: logoWidth, height: logoHeight}" alt="">
       </div>

       <div class="col-md-5 col-md-offset-2">
            <div class="col-md-3">项目库</div>
            <div class="col-md-3">地点库</div>
            <div class="col-md-3">教案库</div>
            <div class="col-md-3">
                <router-link :to="{name: 'home'}" class="login">登陆</router-link>
                <router-link :to="{name: 'home'}" class="register">注册</router-link>
            </div>
       </div>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                msg: 'this is Header component',
                logoSrc:'/static/images/logo.png',
                logoHeight:'80px',
                logoWidth: 'auto'
            }
        }
    }
</script>

<style lang="">
    #header{
        height: 100px;
        padding: 8px 0;
    }
</style>


